import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { MatExpansionModule, MatIconModule, MatToolbarModule, MatFormFieldModule, MatInputModule, MatMenuModule } from '@angular/material';

import { FuseSharedModule } from '@fuse/shared.module';

import { FaqService } from './faq.service';
import { FuseFaqComponent } from './faq.component';
import { MatButtonModule } from '@angular/material/button';
import { FaqFormComponent } from './faq-form/faq-form.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { CommonModule } from '@angular/common';
import { FuseConfirmDialogModule } from '@fuse/components';


const routes = [
    {
        path     : 'faq',
        component: FuseFaqComponent,
        resolve  : {
            faq: FaqService
        }
    }
];

@NgModule({
    declarations: [
        FuseFaqComponent,
        FaqFormComponent
    ],
    imports     : [
        RouterModule.forChild(routes),
        MatExpansionModule,
        MatIconModule,
        FuseSharedModule,
        MatButtonModule,
        CommonModule,
        MatToolbarModule,
        MatFormFieldModule,
        MatInputModule,
        MatMenuModule,
        FuseConfirmDialogModule 
    ],
    providers   : [
        FaqService
    ],
    entryComponents: [FaqFormComponent]
})
export class FaqModule
{
}



