import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';

import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Injectable()
export class FaqService implements Resolve<any>
{
    faqs: any;

    onFaqsChanged: BehaviorSubject<any> = new BehaviorSubject({});

    constructor(private http: HttpClient)
    {
    }

    /**
     * Resolve
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns {Observable<any> | Promise<any> | any}
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
    {
        return new Promise((resolve, reject) => {
            Promise.all([
                this.getFaqs()
            ]).then(
                () => {
                    resolve();
                },
                reject
            );
        });
    }

    /**
     * Get faqs
     */
    getFaqs(): Promise<any[]>
    {
        return new Promise((resolve, reject) => {

            this.http.get('api/faq')
                .subscribe((response: any) => {
                    this.faqs = response;
                    this.onFaqsChanged.next(this.faqs);
                    resolve(this.faqs);
                }, reject);
        });
    }

    newFaq(faq)
    {

    }

    updateFaq(faq)
    {
        return new Promise((resolve, reject) => {

            this.http.post('api/faq/' + faq.id, {...faq})
                .subscribe(response => {
                    this.getFaqs();
                    resolve(response);
            });
        });
    }

    deleteFaq(faq)
    {
        const faqIndex = this.faqs.indexOf(faq);
        this.faqs.splice(faqIndex, 1);
        this.onFaqsChanged.next(this.faqs);
    }
}



