import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormControl, FormGroup} from '@angular/forms';
import { Subscription } from 'rxjs/Subscription';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import { FuseUtils } from '@fuse/utils';
import { FaqService } from './faq.service';

import { MatDialog, MatDialogRef } from '@angular/material';
import { FaqFormComponent } from './faq-form/faq-form.component';
import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';

@Component({
    selector   : 'fuse-faq',
    templateUrl: './faq.component.html',
    styleUrls  : ['./faq.component.scss'],
    animations   : fuseAnimations
})
export class FuseFaqComponent implements OnInit, OnDestroy
{
    faqs: any;
    faqsFiltered: any;
    step = 0;
    searchInput;
    onFaqsChanged: Subscription;

    dialogRef: any;
    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;

    constructor(
        private faqService: FaqService,
        public dialog: MatDialog
    )
    {
        this.searchInput = new FormControl('');
    }

    ngOnInit()
    {
        this.onFaqsChanged =
            this.faqService.onFaqsChanged
                .subscribe(response => {
                    this.faqs = response;
                    this.faqsFiltered = response;
                });

        this.searchInput.valueChanges
            .debounceTime(300)
            .distinctUntilChanged()
            .subscribe(searchText => {
                this.faqsFiltered = FuseUtils.filterArrayByString(this.faqs, searchText);
            });
    }

    initData() {

    }

    ngOnDestroy()
    {
        this.onFaqsChanged.unsubscribe();
    }

    setStep(index: number)
    {
        this.step = index;
    }

    nextStep()
    {
        this.step++;
    }

    prevStep()
    {
        this.step--;
    }

    newFaq()
    {
        this.dialogRef = this.dialog.open(FaqFormComponent, {
            panelClass: 'faq-form-dialog',
            data      : {
                action: 'new'
            }
        });

        this.dialogRef.afterClosed()
            .subscribe((response: FormGroup) => {
                if ( !response )
                {
                    return;
                }

                this.faqService.updateFaq(response.getRawValue());

            });

    }

    updateFaq(faq)
    {
        this.dialogRef = this.dialog.open(FaqFormComponent, {
            panelClass: 'faq-form-dialog',
            data      : {
                faq: faq,
                action : 'edit'
            }
        });

        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response )
                {
                    return;
                }
                const actionType: string = response[0];
                const formData: FormGroup = response[1];
                switch ( actionType )
                {
                    /**
                     * Save
                     */
                    case 'save':

                        this.faqService.updateFaq(formData.getRawValue());

                        break;
                    /**
                     * Delete
                     */
                    case 'delete':

                        this.deleteFaq(faq);

                        break;
                }
            });
    }

    publishFaq(faq)
    {

    }
    
    /**
     * Delete Contact
     */
    deleteFaq(faq)
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to remove?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                this.faqService.deleteFaq(faq);
            }
            this.confirmDialogRef = null;
        });
    }
}



